<template>
  <div id="add-blog">
    <h2>编辑博客</h2>
    <form v-if="!submmited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" required />

      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>

      <div id="checkboxes">
        <label v-rainbow>技术交流</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label v-rainbow>好友聊天</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">
        <label v-rainbow>才能展现</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">
        <label v-rainbow>还是群众演员</label>
        <input type="checkbox" value="Angular4" v-model="blog.categories">
      </div>
      <label>作者:</label>
      <select v-model="blog.author">
        <option v-for="author in authors" :key="author">
          {{author}}
        </option>
      </select>
      <button class="editButton" v-on:click.prevent="post">编辑博客</button>
    </form>

    <div v-if="submmited">
      <h3>您的博客发布成功!</h3>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题: {{blog.title}}</p>
      <p>博客内容:</p>
      <p>{{blog.content}}</p>
      <p>博客分类:</p>
      <ul>
        <li v-for="category in blog.categories" :key="category">
          {{category}}
        </li>
      </ul>
      <p>作者: {{blog.author}}</p>
    </div>
  </div>
</template>

<script>
	import axios from 'axios'
	
export default {
  name: 'add-blog',
  data () {
    return {
    	id:this.$route.params.id,
      blog:{
        
      },
      authors:["ivan","匿名游客","只是过来试一下水"],
      submmited:false
    }
  },
  methods:{
  	fetchData(){
//		console.log(this.id);
     axios.get('https://wd2959516293murkln.wilddogio.com/posts/' + this.id + ".json")
     .then(response =>{
     	    this.blog = response.data;
     })
  	},
    post:function(){
//    this.$http.post("https://wd2959516293murkln.wilddogio.com/posts.json",this.blog)
axios.put('https://wd2959516293murkln.wilddogio.com/posts/' + this.id + ".json",this.blog)
          .then((data)=>{
//          console.log(data);
            this.submmited = true;
          });
    }
  },
  created(){
  	this.fetchData();
  },
    directives:{
  	'rainbow':{
  		bind(el,binding,vnode){
    			 el.style.color = "#" + Math.random().toString(16).slice(2,8);
  		}
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
  box-sizing: border-box;
}

#add-blog{
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}

label{
  display: block;
  margin: 20px 0 10px;
}

input[type="text"],textarea,select{
  display: block;
  width: 100%;
  padding: 8px;
}

textarea{
  height: 200px;
}

#checkboxes label{
  display: inline-block;
  margin-top: 0;
}

#checkboxes input{
  display: inline-block;
  margin-right: 10px;
}

.editButton{
  display: block;
  width: 100px;
  height: 50px;
  margin: 20px 0;
  background: crimson;
  color: #fff;
  border: 0;
  border-radius: 20px;
  font-weight: bold;
  font-size: 18px;
  cursor: pointer;
}

#preview{
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}

h3{
  margin-top: 10px;
}

</style>



