<template>
	<div id="single-blog">
		<h1>{{blog.title}}</h1>
		<article>{{blog.content}}</article>
		<p>作者: {{blog.author}}</p>
		<p>分类:</p>
		<ul>
			<li v-for="category in blog.categories" :key="category">
				{{category}}
			</li>
		</ul>
		<button @click="deleteSingleBlog()">delete</button>
		<router-link class="routerLink" :to="'/edit/' + id">edit</router-link>
	</div>
</template>

<script>
	import axios from 'axios'
	
	export default{
		name:"single-blog",
		data(){
			return{
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
//			this.$http.get('https://wd2959516293murkln.wilddogio.com/posts/' + this.id + ".json")
axios.get('https://wd2959516293murkln.wilddogio.com/posts/' + this.id + ".json")
				.then((data)=>{
					this.blog = data.data;
				})

		},
		methods:{
			deleteSingleBlog(){
				axios.delete('https://wd2959516293murkln.wilddogio.com/posts/' + this.id + ".json")
				.then(response =>{
					this.$router.push({path:'/'});
				})
			}
		}
	}
</script>
<style>
	li{
		list-style: none;
	}
	#single-blog{
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background: #eee;
		border: 1px dotted #aaa;
	}
	button{
		display: inline-block;
		width: 80px;
		height: 40px;
		color: #FFF;
		font-size: 20px;
		font-weight: bold;
		background-color: #CCCCCC;
		border-radius:5px;
	}
	.routerLink{
		display: inline-block;		
		width: 80px;
		height: 38px;
		text-align: center;
		line-height: 40px;
		color: #FFF;
		margin-left: 10px;
		transform: translateY(2px);
		box-shadow: 2px 2px 3px #DC143C;
		font-size: 20px;
		font-weight: bold;
		border: 1px solid #AAAAAA;
		background-color: #f40;
		border-radius:5px;
		text-decoration: none;
	}
</style>


